.main {
  width: 100%;
}
.main .main_header {
  width: 100%;
  height: 40px;
  position: fixed;
  top: 0;
  border-bottom: 1px solid #1890ff;
  box-shadow: 2px 1px 285px 1px #1890ff;
  background-color: #fff;
  z-index: 90;
}
.main .main_banner {
  position: relative;
  padding-top: 40px;
}
.main .main_banner .slick-slide {
  overflow: hidden;
}
.main .main_banner img {
  width: 100%;
  height: 100%;
}
.main .main_navigation .slick-slide {
  overflow: hidden;
}
.main .main_navigation .main_navigation_content ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.main .main_navigation .main_navigation_content ul li {
  width: 20%;
  height: 60px;
  text-align: center;
  line-height: 60px;
}
.main .main_navigation .main_navigation_content ul li > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 4px 0px;
}
.main .main_navigation .main_navigation_content ul li > div .navigation_icon {
  line-height: 36px;
  font-size: 26px;
}
.main .main_navigation .main_navigation_content ul li > div .navigation_name {
  line-height: 12px;
}
.main .main_videoList {
  margin: 0 5px;
}
.main .main_videoList ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.main .main_videoList ul li {
  width: 50%;
  padding: 5px;
}
.main .main_videoList ul li .main_video {
  position: relative;
}
.main .main_videoList ul li .main_video .main_videoImg img {
  width: 100%;
  border-radius: 5px;
}
.main .main_videoList ul li .main_video .main_videoUser {
  position: absolute;
  width: 100%;
  margin-top: -20px;
  padding: 0 7px;
}
.main .main_videoList ul li .main_video .main_videoUser .main_video_usernum {
  float: left;
  width: 50%;
  overflow: hidden;
}
.main .main_videoList ul li .main_video .main_videoUser .main_video_usernum .main_num {
  float: left;
  margin-left: 3px;
  font-size: 11px;
  color: #fff;
}
.main .main_videoList ul li .main_video .main_videoUser .main_video_usernum .main_videoIcon {
  float: left;
  color: #fff;
}
.main .main_end {
  height: 50px;
  width: 100%;
}
.ant-drawer {
  z-index: 99;
}
